	module.exports = {"data":{"post":{"html":"<p>I've been in Germany too long to be really shocked by anything anymore, but last night I had to let my jaw drop. Christopher was watching the Disney-owned TV channel SuperRTL, which was showing a Christmas special from the cartoon <a href=\"http://en.wikipedia.org/wiki/Recess_(TV_series)\">\"Recess\"</a>. According to the German dub, this boy was dressed as a \"Christmas Tree\" (screenshot from <a href=\"http://www.youtube.com/watch?v=IQGJbGeccl8\">YouTube</a>).</p>\n<p><img src=\"http://www.papascott.de/wordpress/wp-content/uploads/2009/12/recess_xmas.png\" alt=\"TJ dressed as a menorah\" border=\"0\" width=\"450\" height=\"288\" /></p>\n<p>I'm assuming the cause was ignorance rather than anti-Semitism, since I'm sure TV shows are translated quickly rather than carefully. But then again, you never know...</p>","fields":{"title":"Bad Translation","date":"06 Dec 2009","url":"https://papascott.de/2009-12-06-bad-translation/"}}},"pathContext":{"slug":"/2009-12-06-bad-translation/","prev":{"fields":{"title":"What a Long Strange Trip It's Been","date":"05 Dec 2009","slug":"/2009-12-05-what-a-long-strange-trip-its-been/"}},"next":{"fields":{"title":"He's making a list, checking it twice...","date":"23 Dec 2009","slug":"/2009-12-23-hes-making-a-list-checking-it-twice/"}}}}

